<template>
  <li @click="setCurrentSongId(item.id)">
    <div class="num">{{number+1}}</div>
    <div class="recommend-new-box">
      <div class="recommend-new-box2">
        <div class="recommend-new-title one-text">{{item.name}}</div>
        <div class="recommend-singer-box one-text">
          <div>
            <i class="recommend-new-singer" v-if="item.h"></i>
            {{item.ar[0].name}} - {{item.name}}
          </div>
        </div>
      </div>
      <div class="recommend-new-play">
        <span class="recommend-playbtn"></span>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    item: Object,
    number: Number,
  },
  methods: {
    setCurrentSongId(id) {
      
      this.$emit("translate-currentid", {
        songId: id,
      });
    },
  },
};
</script>

