<template>
  <div class="playview">
    <audio :src="currentSongUrl" autoplay></audio>
    <template v-if="currentBar">
      <div class="play-box clearfix">
        <div class="img-box">
          <img class="auto-img" :src="currentBar.al.picUrl" alt @click.stop="isShowSongPage = true" />
        </div>
        <h3 class="fl">{{currentBar.name}}</h3>
        <span class="play-btn fr">
          <img
            src="../assets/images/playbtn.png"
            alt
            class="auto-img"
            v-if="isPaused"
            @click="pauseAudio"
          />
          <img src="../assets/images/pausebtn.png" alt class="auto-img" v-else @click="pauseAudio" />
          <canvas width="40px" height="40px" id="canvas"></canvas>
        </span>
      </div>
      <template v-if="isShowSongPage">
        <div class="full-screen">
          <img :src="currentBar.al.picUrl" alt class="full-screen-blur auto-img" />
          <!-- 返回键 歌名 歌手 -->
          <div class="header">
            <span>
              <img src="../assets/images/reback.png" alt @click.stop="back" />
            </span>
            <div class="song-detail">
              <p class="detail-name">{{currentBar.name}}</p>
              <p
                class="detail-artist"
                v-for="(item,index) in currentBar.ar"
                :key="index"
              >{{item.name}}</p>
            </div>
          </div>

          <!-- 磁盘 -->
          <div class="magnetic-disk">
            <div class="needle" :class="{active:isPaused}">
              <img src="../assets/images/g-needle.png" alt />
            </div>
            <div class="disk">
              <img :src="currentBar.al.picUrl" alt class="auto-img" :class="{ rototing: isPaused }" />
            </div>
          </div>

          <!-- 点击该区域进去歌词界面 -->
          <div class="getlyric" @click="showLyric = !showLyric">
            <!-- 歌词界面 -->
            <div class="lrc" v-if="lyric&&showLyric">
              <vue-scroll ref="scro">
                <ul id="ullrc" v-if="lyric&&showLyric" ref="lyy">
                  <li v-for="(item, index) in lyric" :key="index" ref="li">{{item.text}}</li>
                </ul>
                <ul id="ullrc" v-else>
                  <li>搜索不到歌词~~</li>
                </ul>
              </vue-scroll>
            </div>
          </div>

          <!-- 歌曲控制中心 -->
          <div class="control-song">
            <div class="progress">
              <span class="start">{{ getFormatTime(currentTime / 1000) }}</span>
              <span class="end">{{ getFormatTime(currentBar.dt / 1000) }}</span>
              <van-slider v-model="currentTime" @change="changeCurrentTime" :max="currentBar.dt" />
              <!-- <label for>
                <div class="progress-bar">
                  <div class="pass-progress" ref="pro"></div>
                  <div class="dot" ref="dot"></div>
                </div>
                <input
                  type="range"
                  id="audio-progress"
                  v-model="currentTime"
                  @input="changeCurrentTime"
                  :max="currentBar.dt"
                />
              </label>-->
            </div>
            <ul class="control-center">
              <li v-if="isSequence" @click="randomPlay">
                <img src="../assets/images/loop.png" alt />
              </li>
              <li v-else-if="isLoop" @click="sequencePlay">
                <img src="../assets/images/singlecycle.png" alt />
              </li>
              <li v-else-if="isRandom" @click="loopPlay">
                <img src="../assets/images/random.png" alt />
              </li>

              <li @click="getPreviousSong">
                <img src="../assets/images/previous.png" alt />
              </li>
              <li v-if="isPaused" @click="pauseAudio">
                <img src="../assets/images/play.png" alt />
              </li>
              <li v-else @click="pauseAudio">
                <img src="../assets/images/pause.png" alt />
              </li>

              <li @click="getNextSong">
                <img src="../assets/images/nextsong.png" alt />
              </li>
              <li>
                <img src="../assets/images/musiclist.png" alt @click="showPopup" />
                <van-popup v-model="show" position="right" :style="{ height: '100%' }">
                  <ul id="ullrc" class="musiclist">
                    <Songsheetlistitem
                      v-for="(item,index) in musitList"
                      :key="index"
                      :item="item"
                      :number="index"
                      @translate-currentid="$emit('translate-currentid',$event)"
                    ></Songsheetlistitem>
                  </ul>
                </van-popup>
              </li>
            </ul>
          </div>
        </div>
      </template>
    </template>
  </div>
</template>

<script>
import "../assets/less/playView.less";
import Songsheetlistitem from "../components/Songsheetlistitem";
export default {
  components: {
    Songsheetlistitem,
  },
  props: ["currentSongId", "showSongPage"],
  data() {
    return {
      currentBar: null,
      isPaused: false,
      isShowSongPage: this.showSongPage || false,
      showLyric: false,
      isSequence: true,
      isLoop: false,
      isRandom: false,
      show: false,
      currentTime: 0,
      currentIndex: 0,
      lyric: null,
      musitList: [],
     
    };
  },
  computed: {
    currentSongUrl() {
      return (
        "https://music.163.com/song/media/outer/url?id=" +
        this.currentSongId +
        ".mp3"
      );
    },
  },
  watch: {
    currentSongId: function () {
      this.getCurrentBar();
      this.getLyricData();
      this.isShowSongPage = this.showSongPage;
    },
    isPaused() {
      if (this.isPaused) {
        window.clearInterval(window.animateId);
      } else {
        window.clearInterval(this.animateId);
        window.animateId = this.drawCircle();
      }
    },
    currentBar() {
      this.$nextTick(function () {
        window.clearInterval(this.animateId);
        window.animateId = this.drawCircle();
      });
    },
    currentTime() {
      if (this.showLyric && this.isShowSongPage) {
        this.lyricScroll();
      }
    },
  },

  methods: {
    back() {
      this.isShowSongPage = false;
      this.showLyric = false;
    },
    showPopup() {
      this.show = true;
    },
    //根据点击歌曲id播放歌曲
    getCurrentBar() {
      this.showLyric = false;

      this.axios({
        method: "GET",
        url: "http://music.kele8.cn/song/detail?ids=" + this.currentSongId,
      })
        .then((result) => {
          this.isPaused = false;
          this.currentBar = result.data.songs[0];
          if (this.musitList.length == 0) {
            this.musitList.push(this.currentBar);
          } else {
            for (let i = 0; i < this.musitList.length; i++) {
              if (this.musitList[i].id == this.currentBar.id) {
                break;
              } else {
                if (i == this.musitList.length - 1) {
                  this.musitList.push(this.currentBar);
                }
              }
            }
          }
          sessionStorage.setItem("musicList", JSON.stringify(this.musitList));
          //
        })
        .catch((err) => {});
    },
    //暂停播放
    pauseAudio() {
      let audio = this.$el.querySelector("audio");
      if (this.isPaused) {
        audio.play();
      } else {
        audio.pause();
      }
      this.isPaused = true;
      this.isPaused = audio.paused;
    },
    //canvas画进度
    drawCircle() {
      let audio = this.$el.querySelector("audio");
      let canvas = this.$el.querySelector("canvas");
      let ctx = canvas.getContext("2d");

      let id = window.setInterval(() => {
        this.currentTime = audio.currentTime * 1000;

        ctx.clearRect(0, 0, 40, 40);
        //clearRect() 方法清空给定矩形内的指定像素。

        ctx.beginPath();
        ctx.arc(20, 20, 19, 0, 2 * Math.PI);
        ctx.closePath();
        ctx.strokeStyle = "#ccc";
        ctx.stroke();

        ctx.beginPath();
        ctx.arc(
          20,
          20,
          19,
          -0.5 * Math.PI,
          2 * Math.PI * (this.currentTime / this.currentBar.dt) - 0.5 * Math.PI
        );
        ctx.strokeStyle = "rgb(30, 132, 228)";
        ctx.stroke();
      }, 500);
      return id;
    },
    //获取歌词
    getLyricData() {
      this.axios({
        method: "GET",
        url: "http://music.kele8.cn/lyric?id=" + this.currentSongId,
      })
        .then((result) => {
          this.lyric = result.data.lrc.lyric;
          //
          let lyricArr = [];

          this.lyric.split("\n").forEach((element, index) => {
            //提取出前面的时间
            var t = element.slice(1, 10);

            lyricArr[index] = {
              time: Number(t.slice(0, 2)) * 60 + Number(t.slice(3)),
              text: element.slice(11).trim(),
            };
          });

          lyricArr.pop();
          this.lyric = lyricArr;
        })
        .catch((err) => {});
    },
    //歌词滚动
    lyricScroll() {
      let geciBox = this.$refs.lyy;
      let lyric = this.lyric;

      let lis = this.$refs.li;
      let currentTime = this.currentTime / 1000;

      for (let i = 0; i < lyric.length; i++) {
        if (lyric[i].time >= currentTime) {
          this.currentIndex = i;
          break;
        }
      }
      this.$refs.li.forEach((element, index) => {
        if (index == this.currentIndex) {
          if (index < 1) {
            this.$refs.li[0].classList.add("active");
          } else if (index == lyric.length - 1) {
            this.$refs.li[index].classList.add("active");
          } else {
            this.$refs.li[index - 1].classList.add("active");
          }
        } else {
          this.$refs.li[index].classList.remove("active");
        }
      });

      let listop = this.$refs.li[this.currentIndex].offsetTop;
      let boxtop = geciBox.offsetTop;

      if (listop - boxtop > 220) {
        this.$refs["scro"].scrollTo(
          {
            y: listop - boxtop - 220,
          },
          300,
          "easeInQuad"
        );
      }
    },
    //播放时间
    changeCurrentTime() {
      let audio = this.$el.querySelector("audio");
      audio.currentTime = this.currentTime / 1000;
    },
    //格式化播放时间
    getFormatTime(time) {
      var minute = parseInt(time / 60);
      minute = minute.toString().length === 1 ? "0" + minute : minute;
      var second = parseInt(time % 60);
      second = second.toString().length === 1 ? "0" + second : second;
      return minute + ":" + second;
    },

    //顺序播放
    sequencePlay() {
      let audio = document.querySelector("audio");
      this.isLoop = false;
      this.isSequence = true;
      this.isRandom = false;
      audio.loop = false;
      audio.onended = () => {
        this.getNextSong();
      };
    },
    //随机播放
    randomPlay() {
      let audio = document.querySelector("audio");
      this.isSequence = false;
      this.isLoop = false;
      this.isRandom = true;
      audio.loop = false;
      audio.onended = () => {
        this.getRandomSong();
      };
    },
    //循环播放
    loopPlay() {
      let audio = document.querySelector("audio");
      this.isRandom = false;
      this.isLoop = true;
      this.isSequence = false;
      audio.loop = true;
    },

    //下一首
    getNextSong() {
      let data = JSON.parse(sessionStorage.getItem("musicList"));
      this.showLyric = false;

      let audioSrc = document.querySelector("audio");
      if (this.isRandom) {
        this.getRandomSong();
        return;
      }
      for (let i = 0; i < data.length; i++) {
        if (data[i].id == this.currentBar.id) {
          i++;
          //
          if (i > data.length - 1) {
            i = 0;
          }
          this.currentBar = data[i];
          this.axios({
            method: "GET",
            url: "http://music.kele8.cn/lyric?id=" + data[i].id,
          })
            .then((result) => {
              //
              this.lyric = result.data.lrc.lyric;
              let lyricArr = [];
              this.lyric.split("\n").forEach((element, index) => {
                //截取掉前面的时间
                var t = element.slice(1, 10);
                lyricArr[index] = {
                  time: Number(t.slice(0, 2)) * 60 + Number(t.slice(3)),
                  text: element.slice(11).trim(),
                };
              });
              lyricArr.pop();
              this.lyric = lyricArr;
              //
            })
            .catch((err) => {});

          audioSrc.src =
            "https://music.163.com/song/media/outer/url?id=" +
            data[i].id +
            ".mp3";
        }
      }
    },
    //上一首
    getPreviousSong() {
      let data = JSON.parse(sessionStorage.getItem("musicList"));
      this.showLyric = false;

      let audioSrc = document.querySelector("audio");
      if (this.isRandom) {
        this.getRandomSong();
      }
      for (let i = 0; i < data.length; i++) {
        if (data[i].id == this.currentBar.id) {
          i--;
          //
          if (i < 0) {
            return;
          }
          this.currentBar = data[i];
          this.axios({
            method: "GET",
            url: "http://music.kele8.cn/lyric?id=" + data[i].id,
          })
            .then((result) => {
              //
              this.lyric = result.data.lrc.lyric;
              let lyricArr = [];
              this.lyric.split("\n").forEach((element, index) => {
                //截取掉前面的时间
                var t = element.slice(1, 10);
                lyricArr[index] = {
                  time: Number(t.slice(0, 2)) * 60 + Number(t.slice(3)),
                  text: element.slice(11).trim(),
                };
              });
              lyricArr.pop();
              this.lyric = lyricArr;
              //
            })
            .catch((err) => {});

          audioSrc.src =
            "https://music.163.com/song/media/outer/url?id=" +
            data[i].id +
            ".mp3";
        }
      }
    },
    //随机
    getRandomSong() {
      let data = JSON.parse(sessionStorage.getItem("musicList"));
      this.showLyric = false;

      let audioSrc = document.querySelector("audio");
      let randomIndex = Math.floor(Math.random() * (data.length - 1));

      this.currentBar = data[randomIndex];
      this.axios({
        method: "GET",
        url: "http://music.kele8.cn/lyric?id=" + data[randomIndex].id,
      })
        .then((result) => {
          //
          this.lyric = result.data.lrc.lyric;
          let lyricArr = [];
          this.lyric.split("\n").forEach((element, index) => {
            //截取掉前面的时间
            var t = element.slice(1, 10);
            lyricArr[index] = {
              time: Number(t.slice(0, 2)) * 60 + Number(t.slice(3)),
              text: element.slice(11).trim(),
            };
          });
          lyricArr.pop();
          this.lyric = lyricArr;
          //
        })
        .catch((err) => {});

      audioSrc.src =
        "https://music.163.com/song/media/outer/url?id=" +
        data[randomIndex].id +
        ".mp3";
    },
  },
};
</script>

<style lang="less" scoped>
</style>